<script>
import {mapState} from 'vuex'
import AppLoading from '@/components/Loading'
import AppErrorMessage from '@/components/ErrorMessage'
import AppModal from '@/components/Modal'

export default {
  name: 'AppLesson7',
  components: {
    AppLoading,
    AppErrorMessage,
    AppModal
  },

  data() {
    return {
      id: 7,
      code: {html: 'html', js: 'js'},
      isModal: false
    }
  },

  computed: {
    ...mapState({
      isLoading: state => state.course.isLoading,
      error: state => state.course.error
    }),

    getLesson() {
      return this.$store.getters.getLessonById(this.id)
    },

    getOffset() {
      let paddingOffset = 0
      if (this.isModal) {
        paddingOffset = window.innerWidth - document.body.offsetWidth + 'px'
      }
      return paddingOffset
    }
  },

  watch: {
    isModal() {
      if (this.isModal) {
        document.body.style.paddingRight = this.getOffset
        document.body.style.overflow = 'hidden'
      } else {
        document.body.style.paddingRight = 0
        document.body.style.overflow = 'auto'
      }
    }
  },

  methods: {
    openModal() {
      window.scrollTo(0, 0)
      this.isModal = true
    }
  }
}
</script>
