          ><code :data-code=code.html class="code"><span class="block">&lt;form v-if="!isForm" @submit.prevent="checkForm"&gt; &middot;&middot;&middot;  </span><span class="block">&lt;/form&gt;</span><span class="block">&lt;div v-else&gt;</span><span class="block">  &lt;h1&gt; , {<span>{</span> form.login }}!&lt;/h1&gt;</span><span class="block">&lt;/div&gt;</span></code></pre>
