      <!-- End detail -->
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import AppLoading from '@/components/Loading'
import AppErrorMessage from '@/components/ErrorMessage'
import {validationMixin} from 'vuelidate'
import {required, minLength, maxLength, email} from 'vuelidate/lib/validators'

export default {
  name: 'AppLesson6',
  components: {
    AppLoading,
    AppErrorMessage
  },

  mixins: [validationMixin],

  data() {
    return {
      id: 6,
      code: {html: 'html', js: 'js', css: 'css'},
      isForm: false,
      form: {
        login: '',
        email: '',
        password: '',
        gender: 'male',
        country: 'Russia',
        category: ['progress'],
        isAgree: false
      },
      countries: [
        {
          label: '',
          value: 'Russia'
        },
        {
          label: '',
          value: 'Belarus'
        },
        {
          label: '',
          value: 'Kazakhstan'
        }
      ],
      categories: [
        {
          label: '',
          value: 'progress'
        },
        {
          label: '',
          value: 'culture'
        },
        {
          label: '',
          value: 'society'
        },
        {
          label: '',
          value: 'system'
        },
        {
          label: '',
          value: 'power'
        }
      ]
    }
  },

  validations: {
    form: {
      login: {required, minLength: minLength(3)},
      email: {required, email},
      password: {required, minLength: minLength(8)},
      category: {maxLength: maxLength(3)},
      isAgree: {mustBeTrue: value => value}
    }
  },

  computed: {
    ...mapState({
      isLoading: state => state.course.isLoading,
      error: state => state.course.error
    }),

    getLesson() {
      return this.$store.getters.getLessonById(this.id)
    }
  },

  methods: {
    checkForm() {
      this.$v.form.$touch()
      if (!this.$v.form.$error) {
        this.isForm = true
      }
    }
  }
}
</script>
