<template>
  <div class="info">
    <app-loading v-if="isLoading" class="my-2 py-2" />
    <app-error-message v-if="error" :message="error" class="my-2 py-2" />

    <div v-if="getLesson.id">
      <h4 class="my-2 py-2 border-b">
        <span class="title"> #{{ getLesson.id }}&nbsp;/&nbsp;</span>
        <span>{{ getLesson.title }}</span>
      </h4>
      <!-- Start task -->
      <div class="mb-2">
        <span class="title">:&nbsp;</span>    
           .      .
      </div>
      <!-- END task -->

      <!-- Start result -->
      <div class="mb-2">
        <div>
          <input
            type="text"
            @input="searchText = $event.target.value"
            class="input w-full md:w-auto"
            placeholder="..."
          />
        </div>
        <ul>
          <li v-for="(name, i) in getFilteredNames" :key="i" class="px-1 pt-1">
            {{ name }}
          </li>
        </ul>
      </div>
      <!-- End result -->

      <!-- Start detail -->
      <div>
        <h4 class="title">:</h4>
        <div>
            data  : names -  , 
                searchText - 
          ,    ,   input.
          <pre
            class="code-wrapper "
          ><code :data-code=code.js class="code"><span class="block">data() {</span><span class="block">  return {</span><span class="block">    names: ['', '', '', '', ''],  </span><span class="block">    searchText: ''</span><span class="block">  }</span><span class="block">}</span></code></pre>
        </div>
        <div>
             <span class="title">computed</span>   -
          getFilteredNames.      names
              searchText.     
             toLowerCase.
          <pre
            class="code-wrapper"
          ><code :data-code=code.js class="code"><span class="block title">computed: {</span><span class="block">  getFilteredNames() {</span><span class="block">    return this.names.filter(name => name.toLowerCase().includes(this.searchText.toLowerCase()))  </span><span class="block">  }</span><span class="block">}</span></code></pre>
        </div>
        <div>
            ,   input   ul   
            .  
          <span class="title">v-on</span>   ,  
            input.   ,  
          <span class="title">v-for</span>.
          <pre
            class="code-wrapper"
          ><code :data-code=code.html class="code"><span class="block">&lt;div&gt;</span><span class="block">  &lt;input</span><span class="block">    type="text"</span><span class="block">    <span class="title">v-on</span>:input="searchText = $event.target.value" </span><span class="block">    placeholder="..."</span><span class="block">  /></span><span class="block">&lt;/div&gt;</span><span class="block">&lt;ul&gt;</span><span class="block">  &lt;li <span class="title">v-for</span>="(name, i) in getFilteredNames" :key="i" class="p-1"&gt;  </span><span class="block">    {<span>{</span> name <span>}</span>}</span><span class="block">  &lt;/li&gt;</span><span class="block">&lt;/ul&gt;</span></code></pre>
        </div>
      </div>
      <!-- End detail -->
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import AppLoading from '@/components/Loading'
import AppErrorMessage from '@/components/ErrorMessage'

export default {
  name: 'AppLesson2',
  components: {
    AppLoading,
    AppErrorMessage
  },

  data() {
    return {
      id: 2,
      code: {html: 'html', js: 'js'},
      names: ['', '', '', '', ''],
      searchText: ''
    }
  },

  computed: {
    ...mapState({
      isLoading: state => state.course.isLoading,
      error: state => state.course.error
    }),

    getLesson() {
      return this.$store.getters.getLessonById(this.id)
    },

    getFilteredNames() {
      return this.names.filter(name =>
        name.toLowerCase().includes(this.searchText.toLowerCase())
      )
    }
  },

  methods: {}
}
</script>
